import createReducer, { ReducerHandler } from "_helpers/createReducer";
import { IUser } from "data/user";
import { ActionTypes } from "data/actionTypes";
import * as mutators from "../users/mutations";

export interface IUserState {
  users: IUser[];
  loading: boolean;
  deleting: boolean;
  registering: boolean;
  error?: Error;
  editing?: Boolean;
}

const initialState: IUserState = {
  users: [],
  loading: false,
  deleting: false,
  registering: false,
  error: undefined,
  editing: false
};

const handlers: ReducerHandler<IUserState> = {
  [ActionTypes.REGISTER_REQUEST]: mutators.registerRequest,
  [ActionTypes.REGISTER_SUCCESS]: mutators.registersuccess,
  [ActionTypes.REGISTER_FAILURE]: mutators.registerFailure,

  [ActionTypes.GET_USERS_REQUEST]: mutators.getUsersRequest,
  [ActionTypes.GET_USERS_SUCCESS]: mutators.getUsersSuccess,
  [ActionTypes.GET_USERS_FAILURE]: mutators.getUsersFailure,

  [ActionTypes.EDIT_USER_REQUEST]: mutators.editUserRequest,
  [ActionTypes.EDIT_USER_SUCCESS]: mutators.editUserSuccess,
  [ActionTypes.EDIT_USER_FAILURE]: mutators.editUserFailure,

  [ActionTypes.REMOVE_USER_REQUEST]: mutators.deleteUserRequest,
  [ActionTypes.REMOVE_USER_SUCCESS]: mutators.deleteUserSuccess,
  [ActionTypes.REMOVE_USER_FAILURE]: mutators.deleteUserFailure
};

export const userReducer = createReducer<IUserState>(initialState, handlers);
