import React, { useState } from "react";
import { IEntry, IEntryState } from "data/entry";
import { listIcons } from "data/featherIcons"

interface IProps {
    collapseStatus: boolean
    setCollapseStatus: any
}

export const Collapse: React.FunctionComponent<IProps> = (props) => {
    const { collapseStatus, setCollapseStatus } = props;
    // const [status, setStatus] = useState(false)
    const [arrowLeftIcon, arrowRightIcon] = listIcons

    return (
        <div className="collapsible" onClick={() => setCollapseStatus(!collapseStatus)}>{collapseStatus ? arrowRightIcon : arrowLeftIcon}</div>
    );
};

export default Collapse;
