import React, { useState, useEffect } from "react";
import { IEntry } from "data/entry";
import { sideviewIcons } from "data/featherIcons";
import { useTypedDispatch } from "hooks/useTypedDispatch";

import { editEntry } from "store/entries/actions";
import { removeEntry } from "store/entries/actions";
import { useFormFields } from "hooks/useFormFields";

interface Iprops {
  selected: IEntry;
}

export const SideView: React.FunctionComponent<Iprops> = (props) => {
  const { id, title, body, date, categoryId, userId } = props.selected;
  const { selected } = props

  const dispatch = useTypedDispatch();
  const [fields, handleFieldChange, setForm] = useFormFields({
    id,
    title,
    body,
    categoryId,
    date: new Date().toJSON().slice(0, 10).replace(/-/g, "/"),
    userId,
  });
  const [editMode, setEditMode] = useState(false);
  const [editIcon, deleteIcon] = sideviewIcons;

  const tabs = ["Activity", "My Notes", "Goal"];
  const [tabMode, setTabMode] = useState(0);

  useEffect(() => {
    setForm(selected)
  }, [selected]);

  useEffect(() => {
    if (editMode) {
      let timer = setTimeout(() => dispatch(editEntry(fields)), 1000);
      return () => {
        clearTimeout(timer);
      };
    }
  }, [fields.title, fields.body]);

  const deleteItem = () => {
    dispatch(removeEntry(id));
  };

  return (
    <div>
      <div className="sideview-functions">
        <div className="item">Last edit at {date}</div>
        <div className={editMode ? "item edit active" : "item edit"} onClick={() => setEditMode(!editMode)}>
          {editIcon}
        </div>
        <div className="item delete" onClick={deleteItem}>
          {deleteIcon}
        </div>
      </div>
      <div className="sideview-item">
        <div className="sideview-item-menus">
          {/* Show all tabs and set selected class  */}
          <div className="tabs">
            {tabs.map((tab, index) => (
              <div
                key={index}
                className={index === tabMode ? "tab selected" : "tab"}
              >
                <a onClick={() => setTabMode(index)}>{tab}</a>
              </div>
            ))}
          </div>
          <div className="complete">
            <input type="submit" value="click to complete" />
          </div>
        </div>
        <div className="sideview-content">
          <div className="sideview-content-title">
            <input
              id="title"
              placeholder="Add title..."
              value={fields.title}
              onChange={handleFieldChange}
              disabled={!editMode}
            />
          </div>
          <div className="sideview-content-body">
            <textarea
              id="body"
              placeholder="Description..."
              value={fields.body}
              onChange={handleFieldChange}
              disabled={!editMode}
            ></textarea>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SideView;
