import React, { useState, FormEvent, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";

import { getUsers, register, editUser, deleteUser } from "store/users/actions";
import { useTypedSelector } from "store/_reducers";
import { IUser, IUserState } from "data/user";
import Input from "./UI/Input";

type Props = RouteComponentProps<void, {}, IUserState>;

export const Admin: React.FC<Props> = () => {
  const users = useTypedSelector(state => state.userReducer.users);
  const [edit, setEdit] = useState(false);
  const initialState = {
    id: 0,
    firstName: "",
    lastName: "",
    username: "",
    password: "",
    role: 0
  }

  const [inputFields, setInputFields] = useState(initialState);
  const [submitted, setSubmitted] = useState(false);

  // Get users for the first time
  useEffect(() => {
    getUsers();
  }, []);

  useEffect(() => {
    if (!edit) {
      setInputFields(initialState);
    }
  }, [edit]);

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    setSubmitted(true);
    if (inputFields && !edit) {
      register(inputFields);
      setInputFields(initialState);
    } else {
      editUser(inputFields);
    }
  };

  const handleEditUser = (user: IUser) => {
    setEdit(true);
    setInputFields(user);
  };

  const handleRemoveUser = (id: number) => {
    deleteUser(id);
  };

  return (
    <section className="admin-page">
      <form name="form" className="register-form" onSubmit={handleSubmit}>
        <h2>New user</h2>
        <div
          className={
            "form-group" + (submitted && !inputFields.firstName ? " has-error" : "")
          }
        >
          <label htmlFor="firstName">First Name</label>
          <Input
            id="firstName"
            name="firstName"
            className="form-control"
            value={inputFields.firstName}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, firstName: target.value })
            }
          />
          {submitted && !inputFields.firstName && (
            <div className="help-block">First Name is required</div>
          )}
        </div>
        <div
          className={
            "form-group" + (submitted && !inputFields.lastName ? " has-error" : "")
          }
        >
          <label htmlFor="lastName">Last Name</label>
          <Input
            id="lastName"
            name="lastName"
            className="form-control"
            value={inputFields.lastName}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, lastName: target.value })
            }
          />
          {submitted && !inputFields.lastName && (
            <div className="help-block">Last Name is required</div>
          )}
        </div>
        <div
          className={
            "form-group" + (submitted && !inputFields.username ? " has-error" : "")
          }
        >
          <label htmlFor="username">Username</label>
          <Input
            id="username"
            name="username"
            className="form-control"
            value={inputFields.username}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, username: target.value })
            }
          />
          {submitted && !inputFields.username && (
            <div className="help-block">Username is required</div>
          )}
        </div>
        <div
          className={
            "form-group" + (submitted && !inputFields.password ? " has-error" : "")
          }
        >
          <label htmlFor="password">Password</label>
          <Input
            type="password"
            id="password"
            name="password"
            className="form-control"
            value={inputFields.password}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, password: target.value })
            }
          />
          {submitted && !inputFields.password && (
            <div className="help-block">Password is required</div>
          )}
        </div>
        <div>
          <label htmlFor="role">Role</label>
          <select
            id="role"
            value={inputFields.role}
            onChange={({ target }) =>
              setInputFields({ ...inputFields, role: +target.value })
            }
          >
            <option value="0">User</option>
            <option value="1">Admin</option>
          </select>
        </div>
        <div className="form-group">
          <button className="btn btn-primary">
            {edit ? "Edit" : "Add new"} {inputFields.role == 0 ? "User" : "Admin"}
          </button>
          {edit && <button onClick={() => setEdit(false)}>Cancel</button>}
        </div>
      </form>
      <h3>All registered users:</h3>
      {users && (
        <ul>
          {users.map((user: IUser) => (
            <li key={user.id}>
              <a onClick={() => handleEditUser(user)}>
                {user.firstName + " " + user.lastName + " | role: " + user.role}
              </a>
              <span>
                {" "}
                - <a onClick={() => handleRemoveUser(user.id)}>Delete</a>
              </span>
            </li>
          ))}
        </ul>
      )}
    </section>
  );
};

export default Admin;
