import React, { useState, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";
import { useTypedSelector } from "store/_reducers";

import Entry from "./Entry/Entry";
import EntryTest from "./Entry/EntryTest";
import Input from "components/UI/Input"
import { routes } from "data/routes";
import { useFormFields } from "hooks/useFormFields";
import { ReactSortable } from "react-sortablejs";

export const Entries: React.FunctionComponent<RouteComponentProps> = props => {
  const entries = useTypedSelector(state => state.entryReducer.entries);
  const [fields, handleFieldChange] = useFormFields({
    searchTerm: ""
  })
  const [searchResults, setSearchResults] = useState(entries);

  // Find object in array by value
  useEffect(() => {
    const results = entries.filter((obj: any) => {
      return Object.keys(obj).some((key) => {
        if (obj[key] !== null) {
          const tempKey = obj[key].toString().toLowerCase();
          const tempSearch = fields.searchTerm.toLowerCase();
          return tempKey.includes(tempSearch);
        }
      })
    })
    setSearchResults(results)
  }, [fields.searchTerm])

  // Update searchResults if entries has been updated, couse we are mapping the searchResults
  useEffect(() => {
    setSearchResults(entries)
  }, [entries])

  const toEntry = () => {
    props.history.push(routes.AddEntry);
  };

  return (
    <section className="entries-page">
      <div className="entries">
        {/* Header */}
        <div className="header">
          <div className="left">
            <h1>Entries</h1>
            <button onClick={toEntry}>add new</button>
          </div>
          <div className="right">
            <Input placeholder="Search..." id="searchTerm" value={fields.searchTerm} onChange={handleFieldChange} />
          </div>
        </div>
        {/* Entries */}
        <ReactSortable className="sortable" list={searchResults} setList={setSearchResults}>
          {searchResults.map(entry => (
            <Entry
              key={entry.id}
              id={entry.id}
              title={entry.title}
              body={entry.body}
              date={entry.date}
              categoryId={entry.categoryId}
              userId={entry.id}
              searchTerm={fields.searchTerm}
            />
          ))}
        </ReactSortable>
      </div>
      <div className="entries-test">
        {searchResults.map(entry => (
          <EntryTest
            key={entry.id}
            id={entry.id}
            title={entry.title}
            body={entry.body}
            date={entry.date}
            categoryId={entry.categoryId}
            userId={entry.id}
            searchTerm={fields.searchTerm}
          />
        ))}
        {/* {searchResults.map(entry => (
          <EntryTest
            key={entry.id}
            id={entry.id}
            title={entry.title}
            body={entry.body}
            date={entry.date}
            userId={entry.id}
            searchTerm={fields.searchTerm}
          />
        ))} */}
      </div>
    </section>
  );
};

export default Entries;
