import React, { useEffect } from "react";
import { IEntry } from "data/entry";
import { useFormFields } from "hooks/useFormFields";
import Input from "components/UI/Input";
import { editEntry } from "store/entries/actions";
import { useTypedDispatch } from "hooks/useTypedDispatch";

interface IextendIEntry extends IEntry {
  searchTerm: string;
}

export const Entry: React.FunctionComponent<IextendIEntry> = props => {
  const dispatch = useTypedDispatch();
  const { id, title, body, date, userId } = props
  const [fields, handleFieldChange] = useFormFields({
    id,
    title,
    body,
    date,
    userId
  });

  useEffect(() => {
    dispatch(editEntry(fields))
  }, [fields.title, props.title])

  return (
    <div>
      <ul>
        <li>
          <Input id="title" placeholder="Titel..." value={fields.title} onChange={handleFieldChange} />
        </li>
      </ul>
    </div>
  );
};

export default Entry;
