import { Mutator } from "_helpers/createReducer";
import { IEntriesState } from "store/_reducers/entry.reducer";
import { IEntry } from "data/entry";

type EntryMutator<P = any> = Mutator<IEntriesState, P>;

export const getSuccess: EntryMutator<IEntry> = (state, data) => {
  const newEntry = state.entries.concat(data);
  return {
    ...state,
    entries: newEntry
  };
};

export const getFailure: EntryMutator<Error> = (state, error) => {
  return {
    ...state,
    error: error
  };
};

export const addEntry: EntryMutator<IEntry> = (state, data) => {
  const newEntry = state.entries.concat(data);
  return {
    ...state,
    entries: newEntry
  };
};

export const editEntry: EntryMutator<IEntry> = (state, data) => {
  const updatedItems = state.entries.map(entry => {
    if (entry.id === data.id) {
      return { ...entry, ...data };
    }
    return entry;
  });
  return { ...state, entries: updatedItems };
};

export const removeEntry: EntryMutator<number> = (state, data) => {
  console.log("removing item by id:", data);
  return {
    ...state,
    entries: state.entries.filter(entry => entry.id !== data)
  };
};

export const getEntryById: EntryMutator<number> = (state, data) => {
  return {
    ...state,
    entries: state.entries.filter(entry => entry.id !== data)
  };
};
