import React from "react";
import { headerIcons } from "data/featherIcons";

interface IProps {
  searchTerm: string;
  handleFieldChange: any;
  setNewMode: any;
  count: number;
}

export const Header: React.FunctionComponent<IProps> = (props) => {
  const { searchTerm, handleFieldChange, setNewMode, count } = props;
  const [searchIcon, plusIcon] = headerIcons;

  return (
    <div className="header">
      <div className="header-state">
        <div className="header-state-title">Activities</div>
        <div className="header-state-count">
          <i>{count}</i>
        </div>
        <div className="header-state-new">
          <a onClick={() => setNewMode(true)}>{plusIcon}</a>
        </div>
      </div>
      <div className="header-search">
        <input
          type="text"
          placeholder="Search for activity"
          id="searchTerm"
          value={searchTerm}
          onChange={handleFieldChange}
        />
      </div>
      <div className="header-filter">
        <div className="item selected">ALL</div>
        <div className="item">DRUPAL</div>
        <div className="item">JIRA</div>
      </div>
    </div>
  );
};

export default Header;
