import { IPureAction } from "store/entries/types";
import { ActionTypes } from "data/actionTypes";
import { IEntry, IEntryState } from "data/entry";
import API from "store/api";
import { store } from "_helpers";

// Fetch request to API
export const fetchEntriesRequest = async () => {
  try {
    const response = await API.get(`posts`);
    return store.dispatch(fetchSuccess(response.data));
  } catch (error) {
    console.log(error);
    return store.dispatch(fetchError(error));
  }
};

// Set entries
export const fetchSuccess = (
  data: IEntryState
): IPureAction<ActionTypes.GET_ENTRIES_SUCCESS, IEntryState> => ({
  type: ActionTypes.GET_ENTRIES_SUCCESS,
  payload: data
});

// Error if fetch has failed
export const fetchError = (
  error: Error
): IPureAction<ActionTypes.GET_ENTRIES_FAILURE, Error> => ({
  type: ActionTypes.GET_ENTRIES_FAILURE,
  payload: error
});

// Add new entry
export const addEntry = (
  data: IEntry
): IPureAction<ActionTypes.ADD_ENTRY, IEntry> => ({
  type: ActionTypes.ADD_ENTRY,
  payload: data
});

// Edit entry
export const editEntry = (
  data: IEntry
): IPureAction<ActionTypes.EDIT_ENTRY, IEntry> => ({
  type: ActionTypes.EDIT_ENTRY,
  payload: data
});

// Remove Entry
export const removeEntry = (
  id: number
): IPureAction<ActionTypes.REMOVE_ENTRY, Number> => ({
  type: ActionTypes.REMOVE_ENTRY,
  payload: id
});

// Remove Entry
export const getEntryById = (
  id: number
): IPureAction<ActionTypes.GET_ENTRY_BY_ID, Number> => ({
  type: ActionTypes.GET_ENTRY_BY_ID,
  payload: id
});
