import React, { useEffect, useRef, useState } from "react";
import { RouteComponentProps } from "react-router-dom";
import { routes } from "data/routes";

import { editEntry } from "store/entries/actions";
import { removeEntry } from "store/entries/actions";
import { IEntry } from "data/entry";
import { useFormFields } from "hooks/useFormFields";
import Input from "components/UI/Input";
import { SunEditor, options } from "components/UI/Editor"
import { useTypedDispatch } from "hooks/useTypedDispatch";

export const EditEntry: React.FunctionComponent<RouteComponentProps<
  void,
  {},
  IEntry
>> = props => {

  const dispatch = useTypedDispatch();
  const { id, title, body, date, userId } = props.location.state
  const [editorBody, setEditorBody] = useState(body)
  const [fields, handleFieldChange] = useFormFields({
    id,
    title,
    body,
    date: new Date().toJSON().slice(0, 10).replace(/-/g, '/'),
    userId
  });
  // const [saving, setSaving] = useState(false)

  const deleteEntry = () => {
    dispatch(removeEntry(props.location.state.id));
    props.history.push(routes.Entries);
  };

  useEffect(() => {
    let timer = setTimeout(() => dispatch(editEntry({ ...fields, body: editorBody })), 1000);
    return () => {
      clearTimeout(timer)
    }
  }, [editorBody, fields.title])

  const handleChange = (text: string) => {
    setEditorBody(text)
  }

  return (
    <section className="edit-entry">
      <form className="entry-editor">
        <div className="entry-form">
          <div className="title-input">
            <Input id="title" placeholder="Titel..." value={fields.title} onChange={handleFieldChange} />
          </div>
          <div className="body-input">
            <SunEditor
              setContents={body}
              setOptions={options}
              onChange={(text: string) => handleChange(text)}
            />
          </div>
          <button
            className="option"
            type="reset"
            value="Reset"
            onClick={deleteEntry}
          >
            Verwijderen
            </button>
        </div>
      </form>
    </section>
  );
};

export default EditEntry;
