import React, { useState, useEffect } from "react";
import { RouteComponentProps } from "react-router-dom";
import { useTypedSelector } from "store/_reducers";
import { useFormFields } from "hooks/useFormFields";
import { ReactSortable } from "react-sortablejs";

import { IEntry } from "data/entry";
import Header from "components/Activities/Header";
import List from "components/Activities/List";
import Collapse from "components/Activities/Collapse";
import SideView from "components/Activities/SideView";
import NewSideView from "components/Activities/NewSideView";


export const Activities: React.FunctionComponent<RouteComponentProps> = () => {
  const activities = useTypedSelector((state) => state.entryReducer.entries);
  const [selected, setSelected] = useState<IEntry>(activities[0]);
  const [searchResults, setSearchResults] = useState(activities);


  const [newMode, setNewMode] = useState(false);
  const [collapseStatus, setCollapseStatus] = useState(false)

  // Kan beter in de header
  const [fields, handleFieldChange] = useFormFields({
    searchTerm: "",
  });

  // Find object in array by value
  useEffect(() => {
    const results = activities.filter((obj: any) => {
      return Object.keys(obj).some((key) => {
        if (obj[key] !== null) {
          const tempKey = obj[key].toString().toLowerCase();
          const tempSearch = fields.searchTerm.toLowerCase();
          return tempKey.includes(tempSearch);
        }
      });
    });
    setSearchResults(results);
  }, [fields.searchTerm]);

  // Update searchResults if entries has been updated, couse we are mapping the searchResults
  useEffect(() => {
    setSearchResults(activities);
    // update selected alleen als
    // if()
    setSelected(activities[0]);
  }, [activities]);

  // Change sideViews by mode number
  const [sideView, setSideView] = useState(
    <SideView selected={selected} />
  );
  useEffect(() => {
    if (newMode) {
      setSideView(<NewSideView setNewMode={setNewMode} setSelected={setSelected} />);
    } else {
      setSideView(<SideView selected={selected} />);
    }
  }, [selected, newMode]);

  return (
    <section className={collapseStatus ? "activities-page collapsed" : "activities-page"}>
      {/* LEFT */}
      <div className="content-left">
        <Header
          searchTerm={fields.searchTerm}
          handleFieldChange={handleFieldChange}
          setNewMode={setNewMode}
          count={activities.length}
        />
        <List
          entries={searchResults}
          searchTerm={fields.searchTerm}
          setSearchResults={setSearchResults}
          selected={selected}
          setSelected={setSelected}
          setNewMode={setNewMode}
        />
      </div>
      <div className="content-collapse">
        <Collapse collapseStatus={collapseStatus} setCollapseStatus={setCollapseStatus} />
      </div>

      {/* RIGHT */}
      <div className="content-right">{sideView}</div>
    </section >
  );
};

export default Activities;
